/*global console*/
import { Store } from "app/redux/Store";
import { MCOService } from 'services/mcoService';
import errorMessages from './errorMessages.json';
import { formatMessage } from "./commonUtils";
// import { useGetTemplateMappings } from '../../../hooks/mcoHooks/useGetTemplateMappings';

const findMRTemplateByPart = (mrLists = {}, partInfo = null) => {
    if (Object.keys(mrLists).includes(partInfo.name)) {
        return mrLists[partInfo.name][0];
    } else {
        return {};
    }

    // return mrLists?.find((fitem) => {
    //     return fitem?.partAndPlant.includes(partInfo.name);
    // });
}
const findBusinessUnitByPlant = (buLists, businessUnit = null) => {
    return buLists?.find((fitem) => {
        return fitem?.businessUnit === businessUnit;
    });
}
const whereUsedChecks = (whereUsedLists, MCOPlantName) => {
    return whereUsedLists?.filter((fitem) => {
        return (fitem?.makeOrBuy?.toLowerCase()) === "make" && fitem?.plantName === MCOPlantName;
    }) || [];
}

const getHighestSeqCurrentPendingOrActiveMRForPart = (partName = "") => {
    const getStorevalue = Store.getState();
    const mrDetails = getStorevalue?.mcoDetails?.mrDetails || [];
    const retunedMRS = Object.keys(mrDetails?.getHighestSeqCurrentPendingOrActiveMR)?.includes(partName) ? mrDetails?.getHighestSeqCurrentPendingOrActiveMR[partName] : [];
    retunedMRS.forEach(eachMR => {
        eachMR.plantName = eachMR.partAndPlant.split('_')[1];
    })
    return retunedMRS || [];
}
const getChildPartDetails = async (responseData) => {
    let collectChildParts = {};
    let collectWhereUsedInParts = {};
    responseData?.mcoaffectedItem.forEach((eachChildItem) => {
        let collectChildPartName = [];
        eachChildItem?.childInfo?.forEach(eachparts => {
            const partsDetailsArr = eachparts.split("|");
            collectChildPartName.push({
                "partAndPlant": `${partsDetailsArr[1]}_${responseData?.mcoPropeties?.plantName}`,
                "plantName": `${responseData?.mcoPropeties?.plantName}`,
                "partName": `${partsDetailsArr[1]}`,
                "partId": `${partsDetailsArr[3]}`,
                "revision": `${partsDetailsArr[2]}`
            });
        });
        collectChildParts[eachChildItem?.name] = collectChildPartName;

        let collectUsedInPartName = [];
        eachChildItem?.whereUsedInfo?.forEach(whereUsedparts => {
            const whereUsedpartsDetailsArr = whereUsedparts.split("|");
            collectUsedInPartName.push({
                "partAndPlant": `${whereUsedpartsDetailsArr[1]}_${responseData?.mcoPropeties?.plantName}`,
                "plantName": `${responseData?.mcoPropeties?.plantName}`,
                "partName": `${whereUsedpartsDetailsArr[1]}`,
                "partId": `${whereUsedpartsDetailsArr[3]}`,
                "revision": `${whereUsedpartsDetailsArr[2]}`
            });
        });
        collectWhereUsedInParts[eachChildItem?.name] = collectUsedInPartName;
    });
    const mcoService = new MCOService();
    let instanceVal = localStorage.getItem('instance');
    let childDetailsInfoResponse = await mcoService.getChildPartDetails({
        childParts: collectChildParts,
        whereUsedInParts: collectWhereUsedInParts,
        instance: instanceVal
    });

    return childDetailsInfoResponse;
}
const checkTemplateExists = (eachItem, onMBOM_NoTemplatesList, currPartChilddetails, collectErrors, plantName) => {
    if (!(currPartChilddetails.length)) {
        collectErrors.push(`${eachItem.name} - highest child template not exist in MR details table. throw error`);
    }
    const slChildrenWithoutMR = [];
    const slChildNotAllowedOnMBOM = [];
    eachItem?.childInfo.forEach((cfitem) => {
        const childDetailsArr2 = cfitem.split("|");
        const childPartName = childDetailsArr2[1];
        const mrData = currPartChilddetails.find((fitem) => {
            return fitem?.partName.includes(childPartName)
        })
        if (!mrData || (mrData && (!mrData.template || mrData.template === "Unassigned"))) {
            slChildrenWithoutMR.push(`${childPartName} : ${plantName}`);
            // collectErrors.push(`${eachItem.name} - highest child template not exist in MR details table. throw error`);//slChildrenWithoutMR
        } else {
            const tempExist2 = onMBOM_NoTemplatesList.map(eachTempRec => eachTempRec.details?.ALLOWABLE_ORG_TEMPLATES).includes(mrData?.template)
            if (tempExist2) {
                slChildNotAllowedOnMBOM.push(`${childPartName} : ${plantName}`);
                //collectErrors.push(`${eachItem.name} - child template not exist in onMBOM_NoTemplatesList on ORGALLOWEDTEMPLATE. throw error`);//slChildNotAllowedOnMBOM
            }
        }
    });
    if (slChildrenWithoutMR?.length) {
        const childMessage = slChildrenWithoutMR?.join('\n');
        const msgtemplate = errorMessages?.messages?.validationMessages.CHILD_PART_MR_NOT_ASSIGNED;
        const msgReplacementArr = [eachItem?.name, childMessage];
        const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
        collectErrors.push(formattedMessage);
    }

    if (slChildNotAllowedOnMBOM?.length) {
        const childMessage = slChildNotAllowedOnMBOM.join('\n');
        const msgtemplate = errorMessages?.messages?.validationMessages?.NotAllowedOnMBOMChildren;
        const msgReplacementArr = [eachItem?.displayType, eachItem?.name, childMessage];
        const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
        collectErrors.push(formattedMessage);
    }
    return collectErrors;
}

const getMVONotAllowedToHaveEBOMValues = (templateMappingsInfo, businessUnit = "",) => {
    
    const finalTemplates = templateMappingsInfo.filter((item1) => {
        return item1.MappingType === 'orgallowedvalues' && item1.BUID === businessUnit && item1?.details?.EBOM_Allowed?.toUpperCase() === 'N';
    }).map(titem => {
        return titem?.details?.ALLOWABLE_ORG_TEMPLATES;
    }) || [];
    return finalTemplates;
}

const primaryToPurchageValidationMapping = (templateMappingsInfo = "", businessUnit = "") => {
    const purchageValidationMapping = templateMappingsInfo.find((item1) => {
        return item1.MappingType === 'buplantmapping' && item1.BUID === businessUnit && item1?.details?.Entity.toUpperCase() === 'NONE OR PO ORG';
    });
    if (purchageValidationMapping && Object.keys(purchageValidationMapping).length) {
        const primaryToPurchagePlants = purchageValidationMapping.details?.PLANT;
        Object.keys(primaryToPurchagePlants).forEach(eachKey => {
            if (primaryToPurchagePlants[eachKey] === 'None' || primaryToPurchagePlants[eachKey] === '') {
                delete primaryToPurchagePlants[eachKey];
            }
        })
        return primaryToPurchagePlants;
    } else {
        return {};
    }
}

const groupByValues = (anyObject) => {
    const purchagetoPrimaryMap = new Map();
    let purchageValue = '';
    Object.keys(anyObject)?.map(eachKey => {
        purchageValue = anyObject[eachKey];
        if (purchagetoPrimaryMap.has(purchageValue)) {
            const value = purchagetoPrimaryMap.get(purchageValue)
            purchagetoPrimaryMap.set(purchageValue, [...value, eachKey])
        } else {
            purchagetoPrimaryMap.set(purchageValue, [eachKey])
        }
    })
    return purchagetoPrimaryMap;
}

const purchageToPrimaryValidationMapping = (templateMappingsInfo = [], businessUnit = '') => {
    let primaryToPurchageValidation = primaryToPurchageValidationMapping(templateMappingsInfo, businessUnit);
    return groupByValues(primaryToPurchageValidation);
}

const getRDOOUOrgSetMappings = (templateMappingsInfo = '', businessUnit = '', plantToMatch) => {
    let buplantmapping_RDOChecks = templateMappingsInfo?.find((acitem) => {
        return acitem.MappingType === 'buplantmapping' && acitem.BUID === businessUnit && acitem?.details?.Entity.toUpperCase() === 'RDO OU ORG SET';
    });

    buplantmapping_RDOChecks = (buplantmapping_RDOChecks && Object.keys(buplantmapping_RDOChecks).length) ? buplantmapping_RDOChecks?.details?.PLANT : {};

    let plantGroups = templateMappingsInfo?.find((item1) => {
        return item1.MappingType === 'buplantmapping' && item1.BUID === businessUnit && item1?.details?.Entity.toUpperCase() === 'PLANTGROUP';
    });
    plantGroups = (plantGroups && Object.keys(plantGroups).length) ? plantGroups?.details?.PLANT : {};

    const OUgroupMap = groupByValues(plantGroups);

    let matchedPlantsSet = []
    OUgroupMap.forEach((values, key) => {
        const filteredPlants = values?.filter(plant => buplantmapping_RDOChecks && buplantmapping_RDOChecks[plant] && buplantmapping_RDOChecks[plant].toLowerCase() === 'yes')
        if (filteredPlants?.includes(plantToMatch)) {
            matchedPlantsSet = filteredPlants;
            return matchedPlantsSet;
        }
    })
    return matchedPlantsSet;
}

const getOrgAllowedTemplateMappingForPlant = (templateMappingsInfo, businessUnit = "", plant = "", partType = "") => {
    let finalTemplates = [];
    const orgAllowedTemplatesForPlant = templateMappingsInfo.filter((item1) => {
        return item1.MappingType === 'orgallowedvalues' && item1.BUID === businessUnit && Object.keys(item1?.details?.Plants).includes(plant.toUpperCase()) && item1?.details?.Plants[plant.toUpperCase()] === "Y";
    }) || [];
    const getPartTypeDetails = templateMappingsInfo.filter((item1) => {
        return item1.MappingType === 'parttypetotemplate' && item1.BUID === businessUnit;
    }) || [];
    const parttypetotemplateArr = getPartTypeDetails.filter((item1) => {
        return item1.details.ENGG_ITEM_TYPE === partType;
    }) || [];
    let templatesMappingsArr = [];
    if (parttypetotemplateArr.length) {
        orgAllowedTemplatesForPlant.forEach((item1) => {
            const getPlantBasedList = parttypetotemplateArr[0]?.details?.allowableOrgTemplates.filter((item2) => {
                return item2.org_allowed_template === item1?.details?.ALLOWABLE_ORG_TEMPLATES;
            }) || [];
            if (getPlantBasedList.length) {
                templatesMappingsArr.push(item1);
            }
        });
    } else {
        const flatenPartTypeTemplates = getPartTypeDetails?.map(partTypeTemplate => partTypeTemplate.details?.allowableOrgTemplates)?.flat();
        const templatesForAllTypes = flatenPartTypeTemplates?.filter(eachRec => eachRec.exclusive_use?.toLowerCase() === 'yes')?.map(template => template.org_allowed_template)
        templatesMappingsArr = orgAllowedTemplatesForPlant?.filter(eachTemplate => !(templatesForAllTypes?.includes(eachTemplate?.details?.ALLOWABLE_ORG_TEMPLATES)))
    }

    if (templatesMappingsArr.length) {
        finalTemplates = templatesMappingsArr?.map((tItem) => {
            return tItem?.details?.ALLOWABLE_ORG_TEMPLATES
        });
    }
    return finalTemplates;
}

const getFromTOmvoAllowedChangesOracleTemplates = ({ templateMappingsInfo, businessUnit = "", plant = "", prevTemplateName = "" }) => {
    let finalTemplates = [];
    const fromTOmvoAllowedChangesOracleTemplates = templateMappingsInfo.filter((item1) => {
        return item1.MappingType === 'mvoallowedchanges' && item1.BUID === businessUnit && item1?.details?.MVO_TEMPLATE_FROM === prevTemplateName;
    }) || [];

    if (fromTOmvoAllowedChangesOracleTemplates.length) {
        finalTemplates = fromTOmvoAllowedChangesOracleTemplates[0]?.details?.MVO_TEMPLATES_TO?.map((tItem) => {
            return tItem.MVO_TEMPLATE_TO
        }) || []
    }
    return finalTemplates;
}

const getMVOTemplateCompatibilityOracleTemplates = ({ templateMappingsInfo, businessUnit = "", plant = "", templateName = "" }) => {
    let finalTemplates = [];
    const MVOTemplateCompatibilityOracleTemplates = templateMappingsInfo.filter((item1) => {
        return item1.MappingType === 'mvoTemplateCompatibility' && item1.BUID === businessUnit && item1?.details?.MVO_TEMPLATE_OPTION === templateName;;
    }) || [];

    if (MVOTemplateCompatibilityOracleTemplates.length) {
        finalTemplates = MVOTemplateCompatibilityOracleTemplates[0]?.details?.allowableOrgTemplates?.filter(fiItem => fiItem.status?.toLowerCase() === 'active')?.map((tItem) => {
            return tItem.ALLOWABLE_ORG_TEMPLATE
        }) || []
    }
    return finalTemplates;
}

const getMustBeAssignedToPurchaseValidationPlantList = (templateMappingsInfo = '', businessUnit = "") => {

    const finalTemplates = templateMappingsInfo.filter((item1) => {
        return item1.MappingType === 'orgallowedvalues' && item1.BUID === businessUnit && item1?.details?.PO_Org_Exist_Check?.toUpperCase() === 'Y';
    }) || [];

    return finalTemplates;
}

