const getSetCompatibleTemplates = (templateMappingsInfo = '', businessUnit = "", templateName) => {
    let finalTemplates = []
    const getMappedTemplatesList = templateMappingsInfo.filter((item1) => {
        return item1.MappingType === 'setcompatibletemplates' && item1.BUID === businessUnit && item1?.details?.TEMPLATE_ORG_EXISTING === templateName;
    }) || [];
    if (getMappedTemplatesList.length) {
        finalTemplates = getMappedTemplatesList[0]?.details?.TEMPLATE_ORG_MODIFYING?.map((tItem) => {
            return tItem.TEMPLATE_ORG_MODIFYING
        })
    }
    return finalTemplates;
}
