const dropdownValidation = async (responseData) => {
    const getStorevalue = Store.getState();
    const templateList = getStorevalue.mcoDetails.templates || [];
    const orgAllowedTemplateMappingsList = getStorevalue.mcoDetails.ORGAllowedTemplateMappings || [];
    const templateMappingsInfo = getStorevalue.mcoDetails.templateMappingsInfo || [];
    const businessUnitInfo = getStorevalue.mcoDetails.businessUnitList || [];
    const mrDetails = getStorevalue.mcoDetails.mrDetails || [];

    let combineAllErrors = [];
    const childPartAndWhereUsedMRDetails = await getChildPartDetails(responseData);

    responseData?.mcoaffectedItem.forEach((eachItem) => {
        let collectErrors = [];
        const currentPartBuInfo = findBusinessUnitByPlant(businessUnitInfo[responseData?.mcoPropeties?.name], eachItem?.organization);
        const mcoData = responseData.mcoPropeties;
        const MCOPlantName = mcoData?.plantName;
        const partTypeDisplayName = eachItem?.displayType;
        const activeMRInfo = findMRTemplateByPart(mrDetails?.activeMR, eachItem) || {};
        const previousSeqMRInfo = findMRTemplateByPart(mrDetails?.previousSeqMR, eachItem) || {};
        const getHighestSeqCurrentOrActiveMR = findMRTemplateByPart(mrDetails?.getHighestSeqCurrentOrActiveMR, eachItem) || {};

        let whereUsedMRinfo = null;
        if (Object.keys(childPartAndWhereUsedMRDetails?.whereUsedInParts).includes(eachItem?.name)) {
            whereUsedMRinfo = whereUsedChecks(childPartAndWhereUsedMRDetails?.whereUsedInParts[eachItem?.name], MCOPlantName);
        }

        let bHasPrependingActiveMRs = false;
        if (activeMRInfo?.ERPStatus?.toLowerCase() === 'active') {
            bHasPrependingActiveMRs = true;
        }
        let MVOOracleTemplate = '';
        // Is current template is unassigned? If unassigned then throw error.
        if (activeMRInfo.template === undefined || activeMRInfo.template === "") {
            // for error handling 
            const msgtemplate = errorMessages.messages.validationMessages.TEMPLATE_UNASSIGN;
            const msgReplacementArr = [partTypeDisplayName, eachItem?.name];
            const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
            collectErrors.push(`${formattedMessage} ${responseData?.mcoPropeties?.plantName}`);
        }
        if ((Number(activeMRInfo?.sequence)) === 1 && Object.keys(previousSeqMRInfo).length) {
            activeMRInfo.sequence = previousSeqMRInfo?.sequence + 1;
        }
        if ((activeMRInfo.sequence > 1 && (activeMRInfo?.template !== previousSeqMRInfo?.template)) || !(activeMRInfo.sequence > 1)) {
            // Is template is in oracle existing template and Part revision format is not compatiblethen throws error.
            const skip_partRevision = true;

            const pattern = /^\d{2}$/;
            const matches = eachItem?.revision?.match(pattern);
            const getCurrentPartBuInfo = findBusinessUnitByPlant(businessUnitInfo[responseData?.mcoPropeties?.name], eachItem?.organization)
            const sAnyItemOracleExisting = getCurrentPartBuInfo?.oracleExisting?.some(etemplate => etemplate === activeMRInfo.template);
            if (sAnyItemOracleExisting && matches) {
                // for error handling 
                const msgtemplate = errorMessages.messages.validationMessages.REV_NOT_COMPATIBLE_WITH_ORACLE_EXISTING;
                const msgReplacementArr = [partTypeDisplayName, eachItem?.name];
                const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                collectErrors.push(`${formattedMessage} ${responseData?.mcoPropeties?.plantName}`);
            }
        }

        // return if we have errors already
        // if (collectErrors.length) {
        //     combineAllErrors = [...combineAllErrors, ...collectErrors];
        //     return;
        // }

        if (activeMRInfo.sequence > 1) {
            // If both oracle templates are not equal and ERP status is Inactive then it throws error.
            if (activeMRInfo?.template !== previousSeqMRInfo?.template && activeMRInfo?.ERPStatus?.toLowerCase() === "inactive") {
                const msgtemplate = errorMessages.messages.validationMessages.CHANGE_ORACLE_TEMPLATE_ON_INACTIVE;
                const msgReplacementArr = [partTypeDisplayName, eachItem?.name];
                const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                collectErrors.push(`${formattedMessage} ${responseData?.mcoPropeties?.plantName}`);
            }

            // If previous ERP status is Active and Current ERP Status is Inactive then need to check for Part where used,
            //  if Part where used is greater than 0 throw error.
            if (previousSeqMRInfo?.ERPStatus?.toLowerCase() === "active" && activeMRInfo?.ERPStatus?.toLowerCase() === "inactive") {
                if (eachItem?.whereUsedInfo?.length > 0 && whereUsedMRinfo?.length > 0) {//where used check If where used info is exist then throw rror
                    const whereUsedParts = whereUsedMRinfo.map(eachMR => eachMR.partName).join(',');
                    const msgtemplate = errorMessages.messages.validationMessages.MBOM_STILL_USED_ON;
                    const msgReplacementArr = [partTypeDisplayName, eachItem?.name];
                    const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                    collectErrors.push(`${formattedMessage} \n ${MCOPlantName} : ${whereUsedParts}`);
                }
            }
        }

        // return if we have errors already
        // if (collectErrors.length) {
        //     return { valid: false, errDesc: collectErrors };
        // }

        if ((activeMRInfo?.ERPStatus?.toLowerCase()) === "active") {
            if ((activeMRInfo.sequence > 1 && (activeMRInfo?.template !== previousSeqMRInfo?.template)) || !(activeMRInfo.sequence > 1)) {
                // Check Oracle template is exist in NotAllowedOnMBOMTemplateList then (BU and onMBOM==no)
                // If part is used in any MBOM by checking part WhereUsedinPlant then error like template not allowed (WhereUsedinPlant!=0 throw error)
                const onMBOM_NoTemplatesList = orgAllowedTemplateMappingsList?.filter((tmitem) => {
                    return tmitem?.details?.On_MBOM === "N"
                });
                const IstempExists_onMBOM_NoTemplatesList = onMBOM_NoTemplatesList?.find(etemp => {
                    return etemp?.details?.ALLOWABLE_ORG_TEMPLATES === activeMRInfo?.template;
                });

                if (IstempExists_onMBOM_NoTemplatesList && eachItem?.whereUsedInfo?.length > 0 && whereUsedMRinfo?.length > 0) {
                    const whereUsedParts = whereUsedMRinfo.map(eachMR => eachMR.partName).join(',');
                    const msgtemplate = errorMessages.messages.validationMessages.MBOM_STILL_USED_ON;
                    const msgReplacementArr = [partTypeDisplayName, eachItem?.name];
                    const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                    collectErrors.push(`${formattedMessage} \n ${MCOPlantName} : ${whereUsedParts}`);
                }

                if (activeMRInfo?.makeOrBuy === "make") {

                    if (!(eachItem?.childInfo?.length) && !(currentPartBuInfo?.makeWithoutBOMList?.includes(activeMRInfo?.template))) {
                        const msgtemplate = errorMessages.messages.validationMessages.HAS_MBOM_WITH_NO_CHILDREN;
                        const msgReplacementArr = [partTypeDisplayName, eachItem?.name, MCOPlantName];
                        const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                        collectErrors.push(formattedMessage);
                    } else if (eachItem?.childInfo?.length) {
                        collectErrors = checkTemplateExists(eachItem, onMBOM_NoTemplatesList, childPartAndWhereUsedMRDetails?.childParts[eachItem?.name], collectErrors, MCOPlantName);
                    }
                }
            }
        }

        // return if we have errors already
        if (collectErrors.length) {
            combineAllErrors = [...combineAllErrors, ...collectErrors];
            return;
        }

        // if any errors in this stage just throw and skip the remaining process
        if (MCOPlantName === 'MVO') {
            if ((activeMRInfo?.ERPStatus?.toLowerCase()) === 'active') {
                if ((activeMRInfo.sequence > 1 && (activeMRInfo?.template !== previousSeqMRInfo?.template)) || !(activeMRInfo.sequence > 1)) {
                    const mvoAllowedTemplates = getOrgAllowedTemplateMappingForPlant(templateMappingsInfo, mcoData.businessUnit, MCOPlantName, eachItem?.displayType);
                    if (mvoAllowedTemplates?.includes(activeMRInfo?.template)) {
                        let bMVOTemplateFromToIsAllowed = true;
                        if (activeMRInfo.sequence > 1) {
                            const fromToMVOAllowedChanges = getFromTOmvoAllowedChangesOracleTemplates({
                                templateMappingsInfo,
                                businessUnit: mcoData.businessUnit,
                                plant: 'MVO',
                                prevTemplateName: previousSeqMRInfo?.template
                            });
                            if (!(fromToMVOAllowedChanges.includes(activeMRInfo?.template)) && (activeMRInfo?.template !== previousSeqMRInfo?.template)) {
                                bMVOTemplateFromToIsAllowed = false;
                                const msgtemplate = errorMessages.messages.validationMessages.FromToNotAllowed;
                                const msgReplacementArr = [partTypeDisplayName, eachItem?.name, MCOPlantName, activeMRInfo?.template, previousSeqMRInfo?.template];
                                const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                                collectErrors.push(formattedMessage);
                                // throw error that current mvo template is not allowed slTemplateFromToNotAllowed
                            }
                        }
                        if (bMVOTemplateFromToIsAllowed) {
                            const mvoTemplateCompatibleTemplates = getMVOTemplateCompatibilityOracleTemplates({ templateMappingsInfo, businessUnit: mcoData.businessUnit, plant: "", templateName: activeMRInfo?.template });
                            const currentMVOOracleTemplate = activeMRInfo?.template;
                            let mvoTemplateCompatible = true;
                            getHighestSeqCurrentPendingOrActiveMRForPart(eachItem.name).forEach(eachMR => {
                                if (eachMR.plantName !== 'MVO') {
                                    if (eachMR.template && eachMR.template !== "Unassigned" && !(mvoTemplateCompatibleTemplates.includes(eachMR.template))) {
                                        mvoTemplateCompatible = false
                                        const msgtemplate = errorMessages.messages.validationMessages.NotCompatible;
                                        const msgReplacementArr = [partTypeDisplayName, eachItem?.name, eachMR.template, MCOPlantName, currentMVOOracleTemplate];
                                        const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                                        collectErrors.push(formattedMessage);
                                    }
                                }
                            })
                            if (mvoTemplateCompatible) {
                                const mvoNotAllowedToHaveEBOM = getMVONotAllowedToHaveEBOMValues(templateMappingsInfo, mcoData.businessUnit);
                                if (mvoNotAllowedToHaveEBOM.includes(activeMRInfo?.template)) {
                                    if (eachItem.childInfo.length > 0) {
                                        //throw error that template is not allowed to have EBOM 3799
                                        const msgtemplate = errorMessages.messages.validationMessages.NotAllowedToHaveEBOM;
                                        const msgReplacementArr = [partTypeDisplayName, eachItem?.name, currentMVOOracleTemplate];
                                        const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                                        collectErrors.push(formattedMessage);
                                    }
                                }


                            }
                        }

                    } else {
                        //throw error that template does not contains in mvo allowed templates
                        const msgtemplate = errorMessages.messages.validationMessages.NotAllowedMVO;
                        const msgReplacementArr = [partTypeDisplayName, eachItem?.name, activeMRInfo?.template];
                        const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                        collectErrors.push(formattedMessage);
                    }
                }

            } else {
                //MVO ERP status not active rules here
                // 3797 rules should be added here
                const allPlantsWithActiveMR = []
                getHighestSeqCurrentPendingOrActiveMRForPart(eachItem.name).forEach(eachMR => {
                    if (eachMR.plantName !== 'MVO') {
                        allPlantsWithActiveMR.push(eachMR.plantName);
                    }
                })
                const msgtemplate = errorMessages.messages.validationMessages.StillActiveInOtherPlants;
                const msgReplacementArr = [partTypeDisplayName, eachItem?.name];
                const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                collectErrors.push(`${formattedMessage} ${allPlantsWithActiveMR.join(',')}`);
            }
        } else {
            // rule of 3815 need to added here
            //gethighest sequence current or Active MR details and get the status of MVO plant 
            MVOOracleTemplate = getHighestSeqCurrentOrActiveMR?.template;
            if (bHasPrependingActiveMRs) {
                if (!MVOOracleTemplate) {
                    //throw error that 
                    const msgtemplate = errorMessages.messages.validationMessages.MVOMRNotCurrentOrOnThisECOMessage;
                    const msgReplacementArr = [partTypeDisplayName, eachItem?.name];
                    const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                    collectErrors.push(formattedMessage);
                }
            }
        }

        // return if we have errors already
        if (collectErrors.length) {
            combineAllErrors = [...combineAllErrors, ...collectErrors];
            return;
        }

        //Stage 3 code here
        const highestSeqCurrentPendingOrActiveMRForPart = getHighestSeqCurrentPendingOrActiveMRForPart(eachItem.name);
        if (highestSeqCurrentPendingOrActiveMRForPart && highestSeqCurrentPendingOrActiveMRForPart.length > 0) {
            let allowedOracleTemplateValues = [];
            let mvoTemplateCompatibleTemplates = "";
            const sOracleTemplate = activeMRInfo?.template;
            const currentMRSequence = Number(activeMRInfo.sequence);
            if (bHasPrependingActiveMRs) {
                mvoTemplateCompatibleTemplates = getMVOTemplateCompatibilityOracleTemplates({ templateMappingsInfo, businessUnit: mcoData.businessUnit, plant: "", templateName: MVOOracleTemplate });
            }
            if (MCOPlantName !== 'MVO' && ((activeMRInfo?.ERPStatus?.toLowerCase()) === "active") && ((activeMRInfo.sequence > 1 && (activeMRInfo?.template !== previousSeqMRInfo?.template)) || !(activeMRInfo.sequence > 1))) {
                const partMakeOrBuyValue = activeMRInfo?.makeOrBuy;

                const getCurrentPartBuInfo = findBusinessUnitByPlant(businessUnitInfo[responseData?.mcoPropeties?.name], eachItem?.organization)
                const checkIsplantExistOrNot = getCurrentPartBuInfo?.plants.find(buPlant => buPlant?.plantName === MCOPlantName);
                if (((partMakeOrBuyValue.toLowerCase()) === 'make') && !checkIsplantExistOrNot) {
                    const msgtemplate = errorMessages.messages.validationMessages.MakeInNonRDOPlant;
                    const msgReplacementArr = [partTypeDisplayName, eachItem?.name, MCOPlantName];
                    const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                    collectErrors.push(formattedMessage);
                }
                const orgAllowedTemplates = getOrgAllowedTemplateMappingForPlant(templateMappingsInfo, mcoData.businessUnit, MCOPlantName, eachItem?.displayType);
                allowedOracleTemplateValues = mvoTemplateCompatibleTemplates.filter(eachTemplate => orgAllowedTemplates.includes(eachTemplate))
                // primary purchage Validation checks
                const primaryToPurchageMapping = primaryToPurchageValidationMapping(templateMappingsInfo, mcoData.businessUnit);
                const mustBeAssignedToPurchageValidationPlant = getMustBeAssignedToPurchaseValidationPlantList(templateMappingsInfo, mcoData.businessUnit);
                const mustBeAssignedToPurchageValidationPlantTemplates = mustBeAssignedToPurchageValidationPlant.map(eachRecord => eachRecord?.details?.ALLOWABLE_ORG_TEMPLATES);

                const purchagetoPrimaryMap = purchageToPrimaryValidationMapping(templateMappingsInfo, mcoData.businessUnit);
                if (!(purchagetoPrimaryMap.has(MCOPlantName))) {
                    if (allowedOracleTemplateValues && sOracleTemplate && !(allowedOracleTemplateValues.includes(sOracleTemplate))) {
                        //MVOOracleTemplate, sOracleTemplate MCOPlantName
                        const msgtemplate = errorMessages.messages.validationMessages.NotCompatibleOrAllowed;
                        const msgReplacementArr = [partTypeDisplayName, eachItem?.name, sOracleTemplate, MCOPlantName, MVOOracleTemplate];
                        const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                        collectErrors.push(formattedMessage);
                        // throw the error that MVOOracleTemplate and sOracleTemplate are not compatible for plant 4041
                    }
                    if (Object.keys(primaryToPurchageMapping).includes(MCOPlantName)) {
                        if (sOracleTemplate && mustBeAssignedToPurchageValidationPlantTemplates.includes(sOracleTemplate)) {
                            const purchagePlant = primaryToPurchageMapping[MCOPlantName];
                            if (purchagePlant !== "None") {
                                const hasMRForPurchagePlant = highestSeqCurrentPendingOrActiveMRForPart.find(eachMR => purchagePlant === eachMR.partAndPlant.split('_')[1])
                                if (!hasMRForPurchagePlant) {
                                    //throw error Not assigned to purchage validation plant 4060
                                    // MCOPlantName sOracleTemplate purchagePlant
                                    const msgtemplate = errorMessages.messages.validationMessages.NotAssignedToPurchaseValidationPlant;
                                    const msgReplacementArr = [partTypeDisplayName, eachItem?.name, MCOPlantName, sOracleTemplate, purchagePlant];
                                    const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                                    collectErrors.push(formattedMessage);
                                }
                            }
                        }
                    }

                } else {
                    let isAssignedToPrimary = false;
                    if (currentMRSequence === 1) {
                        if (allowedOracleTemplateValues && sOracleTemplate && !(allowedOracleTemplateValues.includes(sOracleTemplate))) {
                            // throw the error that MVOOracleTemplate and sOracleTemplate are not compatible for plant 4087
                            const msgtemplate = errorMessages.messages.validationMessages.NotCompatibleOrAllowed;
                            const msgReplacementArr = [partTypeDisplayName, eachItem?.name, sOracleTemplate, MCOPlantName, MVOOracleTemplate];
                            const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                            collectErrors.push(formattedMessage);
                        }

                        const getCurrentPartBuInfo = findBusinessUnitByPlant(businessUnitInfo[responseData?.mcoPropeties?.name], eachItem?.organization)
                        const sAnyItemOracleExisting = getCurrentPartBuInfo?.oracleExisting?.some(etemplate => etemplate === sOracleTemplate);
                        if (!sAnyItemOracleExisting) {// need to write this check again
                            const primaryPlants = purchagetoPrimaryMap.get(MCOPlantName);
                            primaryPlants?.forEach(eachPrimary => {
                                const hasMR = highestSeqCurrentPendingOrActiveMRForPart.find(eachMR => eachPrimary === eachMR.partAndPlant.split('_')[1])
                                if (hasMR) {
                                    isAssignedToPrimary = true;
                                }
                            })
                            if (!isAssignedToPrimary) {
                                const primaryPlantsStr = primaryPlants.join(" or ");
                                //MCOPlantName primaryPlantsStr
                                const msgtemplate = errorMessages.messages.validationMessages.NotAssignedToPrimaryPlant;
                                const msgReplacementArr = [partTypeDisplayName, eachItem?.name, MCOPlantName, primaryPlantsStr];
                                const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                                collectErrors.push(formattedMessage);
                            }
                        }

                    } else if (currentMRSequence > 1) {
                        if (mvoTemplateCompatibleTemplates && sOracleTemplate && !(mvoTemplateCompatibleTemplates.includes(sOracleTemplate))) {
                            // throw error that template not compatible 4126
                            const msgtemplate = errorMessages.messages.validationMessages.NotCompatible;
                            const msgReplacementArr = [partTypeDisplayName, eachItem?.name, sOracleTemplate, MCOPlantName, MVOOracleTemplate];
                            const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                            collectErrors.push(formattedMessage);
                        }
                    }
                }
            }

            // return if we have errors already
            if (collectErrors.length) {
                combineAllErrors = [...combineAllErrors, ...collectErrors];
                return;
            }

            //stage 4 final
            if (MCOPlantName !== 'MVO') {
                // need to write some more code here
                const partBU = eachItem.organization;
                const plantName = MCOPlantName;
                const OracleTemplate = sOracleTemplate;
                const plantPair = getRDOOUOrgSetMappings(templateMappingsInfo, partBU, MCOPlantName);
                if (plantPair?.includes(plantName)) {
                    let templatesFromEachRDOOUPlants = [];
                    let RDOPlanttemplates = [];
                    plantPair?.forEach(eachPlant => {
                        if (eachPlant !== plantName) {
                            const hasMR = highestSeqCurrentPendingOrActiveMRForPart.find(eachMR => eachPlant === eachMR.partAndPlant.split('_')[1])
                            if (hasMR) {
                                const RDOPlantTemplateName = hasMR.template;
                                RDOPlanttemplates = getSetCompatibleTemplates(templateMappingsInfo, partBU, RDOPlantTemplateName);
                                if (RDOPlanttemplates) {
                                    templatesFromEachRDOOUPlants.push(RDOPlanttemplates);
                                }
                            }
                        }
                    })
                    templatesFromEachRDOOUPlants = templatesFromEachRDOOUPlants.flat();
                    if (templatesFromEachRDOOUPlants?.length && !(templatesFromEachRDOOUPlants.includes(OracleTemplate))) {
                        // throw error using this plantPair
                        const msgtemplate = errorMessages.messages.validationMessages.NonMVONotCompatible;
                        const msgReplacementArr = [partTypeDisplayName, eachItem?.name];
                        const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                        collectErrors.push(`${formattedMessage} \n ${plantPair.join('\n')}`);
                    }
                }

            }
        }
        if (highestSeqCurrentPendingOrActiveMRForPart && highestSeqCurrentPendingOrActiveMRForPart.length > 0) {
            if ((activeMRInfo.sequence > 1 && (activeMRInfo?.template !== previousSeqMRInfo?.template)) || !(activeMRInfo.sequence > 1)) {
                const mvoNotAllowedToHaveEBOM = getMVONotAllowedToHaveEBOMValues(templateMappingsInfo, mcoData.businessUnit);
                if (mvoNotAllowedToHaveEBOM.includes(activeMRInfo?.template)) {
                    if (eachItem.childInfo.length > 0) {
                        //throw error that template is not allowed to have EBOM
                        const msgtemplate = errorMessages.messages.validationMessages.NotAllowedToHaveEBOM;
                        const msgReplacementArr = [partTypeDisplayName, eachItem?.name, MVOOracleTemplate];
                        const formattedMessage = formatMessage(msgtemplate, msgReplacementArr);
                        collectErrors.push(formattedMessage);
                    }
                }
            }
        }

        if (collectErrors.length) {
            combineAllErrors = [...combineAllErrors, ...collectErrors];
            return;
        }
    });

    // collectErrors.push(`Failed.`);

    if (combineAllErrors.length) {
        return { valid: false, errDesc: combineAllErrors };
    } else {
        return { valid: true, errDesc: '' };
    }
}

export {
    dropdownValidation,
    primaryToPurchageValidationMapping,
    getRDOOUOrgSetMappings
}