const instance = localStorage.getItem('instance');
const fetchRSMEAndRMEDetails = () => {
    const mcoService = new MCOService();

    return mcoService.getRSMEList().then(getRSMEList => {
        return getRSMEList.map(ritem => {
            const splitValues = ritem.split('|');
            return {
                label: `${splitValues[1]}(${splitValues[0]})`,
                key: splitValues[0],
                id: splitValues[2],
                email: splitValues[3]
            };
        });
    }).then(rsmeList => {
        return mcoService.getRMEList().then(getRMEList => {
            const rmeList = getRMEList.map(ritem => {
                const splitValues = ritem.split('|');
                return {
                    label: `${splitValues[1]}(${splitValues[0]})`,
                    key: splitValues[0],
                    id: splitValues[2],
                    email: splitValues[3]
                };
            });
            return { rsmeList, rmeList };
        });
    }).then(({ rsmeList, rmeList }) => {
        Store.dispatch(update_generalValues({
            RSMEList: rsmeList,
            RMEList: rmeList
        }));
    }).catch(err => {
        Store.dispatch(update_generalValues({
            isPageHavingIssue: true
        }));
        throw err;
    });
};


const CloneDataFromMaster = (locresData, masterAttData) => {
    let new_mcoPropeties = { ...locresData.mcoPropeties };
    const msInformation = masterAttData;

    if (new_mcoPropeties && (new_mcoPropeties?.startdate === null || new_mcoPropeties?.startdate === '' || new_mcoPropeties?.startdate === "Unassigned")) {
        new_mcoPropeties['startdate'] = (msInformation?.mcoProperties?.startdate) ? (msInformation?.mcoProperties?.startdate) : null;
    }
    if (new_mcoPropeties && (new_mcoPropeties?.rme === null || new_mcoPropeties?.rme === '' || new_mcoPropeties?.rme === "Unassigned")) {
        new_mcoPropeties['rme'] = (msInformation?.mcoProperties?.rme) ? (msInformation?.mcoProperties?.rme) : null;
    }
    if (new_mcoPropeties && (new_mcoPropeties?.rsme === null || new_mcoPropeties?.rsme === '' || new_mcoPropeties?.rsme === "Unassigned")) {
        new_mcoPropeties['rsme'] = (msInformation?.mcoProperties?.rsme) ? (msInformation?.mcoProperties?.rsme) : null;
    }

    // for auto saving
    // submitPropertiesForm("auto");

    return new_mcoPropeties;
}
const prepareItemPayload = async (responseData) => {
    try {
        const getStorevalue = Store.getState();
        const plantData = getStorevalue.mcoDetails.plants || [];
        const sourceData = getStorevalue.mcoDetails.source || [];
        const templateList = getStorevalue.mcoDetails.templates || [];
        const getTargetInfo = prepareTargetInfo(responseData, plantData, sourceData);

        const mcoService = new MCOService();
        let itemData = await mcoService.getItemDetailsByMcoId(responseData?.mcoPropeties?.id);

        const affectedPartsData = await getAffectedPartsData();
        let affectedItems = itemData?.affectedItems.map(gitem => {
            const filteredAffItem = affectedPartsData.filter(material => gitem?.id === material.id);

            let filteredAffItemDetail = null;
            if (filteredAffItem.length && filteredAffItem[0].materialType !== null) {
                filteredAffItemDetail = {
                    name: filteredAffItem[0].materialType.name,
                    attributes: {
                        ...filteredAffItem[0].materialType.attributeMap,
                        'Sales Org': getTargetInfo?.plantInfo?.['SalesOrg'],
                        // ...(responseData?.isMaster === "N" && { 'Copy From': undefined })
                    }
                };

                // remove the field while copy the value from master
                if (responseData?.isMaster === "N") {
                    delete filteredAffItemDetail.attributes['Copy From'];
                }
            }
            gitem.materialType = filteredAffItemDetail;

            // dynamic attributes updates  
            const thisTemp = templateList?.find(titem => titem?.templateName === gitem.materialType?.name) || {};

            if (thisTemp.attributes) {
                thisTemp.attributes.forEach(aitem => {
                    const targetKey = aitem.name;
                    const sourceValue = gitem[targetKey];
                    const sourceData = gitem.attributeGroupData?.[targetKey];
                    const findingMappingValues = Object.keys(aitem.values[0]).find(attritem => aitem.values[0][attritem] === sourceValue);
                    const findingMappingValues2 = Object.keys(aitem.values[0]).find(attritem => aitem.values[0][attritem] === sourceData);

                    if (sourceValue !== undefined) gitem[targetKey] = findingMappingValues;
                    if (sourceData !== undefined) gitem.attributeGroupData[targetKey] = findingMappingValues2;
                });
            }

            return gitem;
        });

        itemData.affectedItems = affectedItems;
        itemData.target = getTargetInfo?.targetInfo;

        return itemData;
    } catch (error) {
        return error;
    }
}
const formatDate = (thisDate) => {
    const dateFormat = dayjs(thisDate).format('YYYYMMDD');
    return dateFormat;
}
const prepareBomPayload = (itemData, getTargetInfo) => {
    let new_itemData = itemData;
    const collectParentItems = [];
    const new_parentItems = new_itemData?.parentItems.map((pItem) => {
        collectParentItems.push(pItem.parentItemNumber);
        if (getTargetInfo?.plantInfo?.ECN_required === "N") {
            pItem.parentItemRevision = '';
            if (pItem?.childItems?.length) {
                const newchildItem = pItem?.childItems?.map((citem) => {
                    citem['childRevision'] = '';
                    citem['effectivityDate'] = formatDate(citem.effectivityDate);
                    return citem;
                })
                pItem.childItems = newchildItem;
            }
        }
        return pItem;
    })
    new_itemData.parentItems = new_parentItems;

    new_itemData['target'] = getTargetInfo?.targetInfo;
    return { bomPayloadData: new_itemData, collectParentItems: collectParentItems };
}
const prepareTargetInfo = (responseData, plantData, sourceData) => {
    const org_id = responseData?.mcoPropeties?.organizationId;
    const sourceUrl = responseData?.source?.url;
    const currplantName = responseData?.mcoPropeties?.plantName;
    let targetInfo = [];
    if (org_id) {
        const orgarr = org_id.split(" ");
        if (orgarr.length) {
            const findSourceInfo = sourceData?.filter((fitem) => {
                return (orgarr[0] === "ORACLE") || (fitem.ERPType === orgarr[0] && fitem.instance === orgarr[1] && sourceUrl === fitem?.source);
            }) || [];
            targetInfo = findSourceInfo;
        }
    }

    return generateTargetInfo(currplantName, plantData, targetInfo)
}

const generateTargetInfo = (currplantName, plantData, targetInfo) => {
    let findPlantInfo = null;
    let findTargetInfo = null;
    if (targetInfo.length) {
        targetInfo = targetInfo[0];

        findPlantInfo = plantData?.filter((fitem) => {
            return fitem.ERPType === targetInfo?.ERPType && (fitem.instance === targetInfo?.environment || fitem.instance === targetInfo?.instance) && fitem.plantName === currplantName;
        }) || null;

        if (findPlantInfo) {
            findPlantInfo = findPlantInfo[0];
            let findPlantName = findPlantInfo?.aliasPlant;

            findTargetInfo = {
                system: targetInfo?.ERPType,
                instance: targetInfo?.environment,
                businessUnit: "BU",
                plant: findPlantName,
                url: findPlantInfo?.ConnectivityDetails?.url,
                username: findPlantInfo?.ConnectivityDetails?.username,
                password: findPlantInfo?.ConnectivityDetails?.password,
                authkey: "",
                port: "",
                sapsysnum: findPlantInfo?.ConnectivityDetails?.sapsysnum,
                sapclientid: findPlantInfo?.ConnectivityDetails?.sapclientid,
                "BOM Usage": findPlantInfo?.bomusage ?? ''
            }
        }
    }
    return {
        targetInfo: findTargetInfo,
        plantInfo: findPlantInfo
    }
}

const updateFinalAcksDetails = (payloadData, callfrom = '') => {
    try {
        const mcoService = new MCOService();
        mcoService.updateFinalAcksDetails(payloadData).then((response) => {
            if (response?.data) {
                const currMcoDetails = response?.data;

                if (callfrom === "autorelease") {
                    return;
                }
                Store.dispatch(update_generalValues({
                    currentMcoDetails: currMcoDetails
                }));
            }
        }).catch((error) => {
            return error;
        });
    } catch (err) {
        throw err;
    }
}

