function deepClone(obj) {
    if (typeof obj !== 'object' || obj === null) {
        return obj; // Return primitives as is
    }

    if (Array.isArray(obj)) {
        return obj.map(deepClone); // Clone arrays recursively
    }

    // Clone objects recursively
    return Object.fromEntries(
        Object.entries(obj).map(([key, value]) => [key, deepClone(value)])
    );
}

const validateForm = (responseData, vType) => {
    let errors = {};
    const labelArr = {
        startDate: "Startdate",
        rme: "Responsible Manufacturing Engineer",
        rsme: "Responsible Senior Manufacturing Engineer",
        mcostate: "State",
        responseFromErp: "Export Status",
        plantName: "Plant",
        description: "Description",
        ECOExportToERP: "ECO Export To ERP"
    };
    if (Object.keys(responseData?.mcoPropeties || {}).length) {
        const values = responseData?.mcoPropeties;
        console.log(values,"valuesvaluesvaluesvalues");


        // Validation logic for each field
        // Assuming values.startdate is a Date object or a string in ISO 8601 format (e.g., '2024-04-19')
        if (!values?.startdate) {
            errors.startdate = `${values?.name}: ${labelArr['startDate']} is required`;
        } else {
            let currentDate = new Date();
            currentDate.setHours(0, 0, 0, 0); // Set current date's time to midnight

            let parts = values?.startdate.split(/[\s/:]+/); // Split the string by space, /, and :
            let startDate = new Date(parts[2], parts[0] - 1, parts[1], parts[3], parts[4], parts[5]); // Year, month (0-based), day, hour, minute, second
            console.log(startDate,"startDate");
            console.log(responseData?.mcoPropeties,"responseData?.mcoPropeties");

            if (startDate < currentDate && responseData?.mcoPropeties.mcostate !== "Release" && responseData?.mcoPropeties.mcostate !== "Create" && responseData?.mcoPropeties.mcostate !== "Review") {                
                errors.startdate = `${values?.name}: ${labelArr['startDate']} must be a present or future date`;
            }

            // if(value.mcostate == "release" ){
            // }
        }

        if(instance !== 'SaaS'){
            if (!values.rme || values.rme?.toLowerCase() === "unassigned") {
                errors.rme = `${values?.name}: ${labelArr['rme']} is required`;
            }
            if (!values.rsme || values.rsme?.toLowerCase() === "unassigned") {
                errors.rsme = `${values?.name}: ${labelArr['rsme']} is required`;
            }
            if (!values.description) {
                errors.description = `${values?.name}: ${labelArr['description']} is required`;
            }
        }
        
        if (!values.ECOExportToERP && vType !== "cavalidation") {
            errors.ECOExportToERP = `${values?.name}: ${labelArr['ECOExportToERP']} is required`;
        }
    }
    return errors;
};

