const validationChecks_old = async (vType, responseData, itemValidationArr) => {
    const getStorevalue = Store.getState();
    const templateMappingsInfo = getStorevalue.mcoDetails.templateMappingsInfo || [];

    let validFieldArr = itemValidationArr;
    let errorCount = {
        isMaTypeHaveErr: true,
        isAttrHaveErr: true,
        isprodHeirarchyHaveErr: true,
    };
    let organizationId = responseData?.mcoPropeties?.organizationId;

    // Validation of PropertiesForm
    const errorsOfPropertiesForm = validateForm(responseData, vType);
    if (Object.keys(errorsOfPropertiesForm).length) {
        return { valid: false, errDesc: errorsOfPropertiesForm };
    }

    const mcoData = responseData.mcoPropeties;
    const MCOPlantName = mcoData?.plantName;

    responseData?.mcoaffectedItem?.forEach((gitem, gkey) => {
        const findMatvalidInd = validFieldArr.findIndex((valItem, valInd) => {
            return (gitem?.id === valItem.id);
        });

        let checkMatValid = true;

        // for twin plant - rdo checks to allow user to save with selecting template
        const partBU = gitem?.organization;
        const plantPair = getRDOOUOrgSetMappings(templateMappingsInfo, partBU, MCOPlantName);
        if (plantPair?.includes(MCOPlantName)) {
            checkMatValid = false;
            errorCount['isMaTypeHaveErr'] = false;
        } else if (gitem?.materialType?.name) {
            checkMatValid = false;
            errorCount['isMaTypeHaveErr'] = false;
        }

        let checkAttValid = true;
        if (organizationId.includes('SAP')) {
            if (gitem?.materialType?.attributeMap !== undefined && Object.keys(gitem?.materialType?.attributeMap).length) {
                checkAttValid = false;
                errorCount['isAttrHaveErr'] = false;
            }

            let checkProducthierachyValid = true;
            if (gitem?.attributeGroupData?.ProductHierarchy && gitem?.attributeGroupData?.ProductHierarchy !== '') {
                checkProducthierachyValid = false;
                errorCount['isprodHeirarchyHaveErr'] = false;
            }

            validFieldArr[findMatvalidInd].materialTypeError = checkMatValid;
            validFieldArr[findMatvalidInd].attributeFormError = checkAttValid;
            validFieldArr[findMatvalidInd].producthierachyError = checkProducthierachyValid;

        } else if (organizationId.includes('ORACLE')) {
            validFieldArr[findMatvalidInd].materialTypeError = checkMatValid;
            validFieldArr[findMatvalidInd].attributeFormError = false; // disable for oracle
            validFieldArr[findMatvalidInd].producthierachyError = false; // disable for oracle
        }
    });

    return callDisplayErrorNotification(errorCount, validFieldArr, organizationId);
}

const callDisplayErrorNotification = (errorCount, itemValidationArr, organizationId) => {
    try {
        let valid = true;
        let errDesc = "";
        if (itemValidationArr?.length) {
            itemValidationArr?.map((item) => {
                if (valid && (item?.materialTypeError || item?.attributeFormError || item?.producthierachyError)) {
                    valid = false
                }
                return { valid: true, errDesc: '' };
            })
        }

        if (!valid) {
            errDesc = 'Please fill mandatory details.';
            if (errorCount?.isMaTypeHaveErr && errorCount?.isAttrHaveErr && organizationId.includes('SAP')) {
                errDesc = "Invalid Material type and attributes.";
            } else if (errorCount?.isMaTypeHaveErr) {
                errDesc = "Invalid Material type.";
            } else if (errorCount?.isAttrHaveErr && organizationId.includes('SAP')) {
                errDesc = "Please fill mandatory attributes.";
            } else if (errorCount?.isprodHeirarchyHaveErr && organizationId.includes('SAP')) {
                errDesc = "Product Hierarchy information missing.";
            }
        }

        return { valid: valid, errDesc: errDesc };
    } catch (err) {
        return { valid: false, errDesc: err?.message };
    }
}

const collectSourcePlantTemplate = async (resData, getMasterDatails = []) => {
    // if(instance === "SaaS")
    // resData.mcoPropeties = resData?.mcoProperties;
    // console.log('resData', resData);
    try {
        const sptPayload = {
            "plantName": resData?.mcoPropeties?.plantName,
            "organizationId": resData?.mcoPropeties?.organizationId,
            "sourceUrl": resData?.source?.url,
            "mcotype": (getMasterDatails[0]?.id === resData?.mcoPropeties?.id) ? "master" : "child",
            "businessUnit": resData?.mcoPropeties?.businessUnit,
        }
        console.log('source payload', sptPayload);
        let materialDataArr = [];
        const mcoService = new MCOService();
        await mcoService.getSourcePlantTemplate(sptPayload).then(async (respdata) => {
            if (respdata) {
                console.log('respdata', respdata);
                console.log('resData?.mcoPropeties?.plantName', resData?.mcoProperties?.plantName);
                respdata.plants.map((plant)=>{
                    if(resData?.mcoPropeties?.plantName){
                        resData.mcoPropeties.organizationId = plant.ERPType + ' ' + plant.instance;
                    }
                })
                console.log('resData?.mcoPropeties?.organizationId', resData);
                materialDataArr = respdata.templates;

                let merge_templates = respdata.templates || [];
                console.log('getMasterDatails', getMasterDatails);
                if ((getMasterDatails[0]?.id && getMasterDatails[0]?.id) && getMasterDatails[0]?.id !== resData?.mcoPropeties?.id) {
                    const mastersptPayload = {
                        "plantName": resData?.mcoaffectedItem[0]?.relatedMCOs[0]?.plantName,
                        "organizationId": resData?.mcoPropeties?.organizationId,
                        "sourceUrl": resData?.source?.url,
                        "mcotype": (getMasterDatails[0]?.id === resData?.mcoaffectedItem[0]?.relatedMCOs[0]?.id) ? "master" : "child",
                        "businessUnit": resData?.mcoPropeties?.businessUnit,
                    }
                    console.log('mastersptPayload', mastersptPayload);
                    await mcoService.getSourcePlantTemplate(mastersptPayload).then((respdata2) => {
                        if (respdata2) {
                            merge_templates = respdata2.templates.concat(respdata.templates);
                        }
                    })
                }

                const ORGAllowedTemplateMappings = respdata?.templateMappingsInfo?.filter((tmitem) => {
                    return tmitem?.MappingType === "orgallowedvalues";
                });

                const final_spt = {
                    source: (respdata?.source),
                    businessUnitList: { [resData?.mcoPropeties?.name]: respdata?.businessUnit },
                    plants: (respdata?.plants),
                    templates: (merge_templates),
                    materialData: (materialDataArr),
                    templateMappingsInfo: (respdata?.templateMappingsInfo),
                    ORGAllowedTemplateMappings: (ORGAllowedTemplateMappings),
                }
                Store.dispatch(update_generalValues(final_spt));
            }
        })
    } catch (err) {
        throw err;
    }
}
