const updateMcoDetailsInDatabase = async (updatedPropetiesValue, responseData, saveMode = "") => {
    try {
        const getpayloadData = [{ id: responseData?.mcoPropeties?.id, instance: instance}];
        const mcoService = new MCOService();
        const resData = await mcoService.getattributesbyid(getpayloadData);

        let payloadData = {};
        if ((Object.keys(resData?.data).length && resData?.data[0]) && saveMode === "saveMcoState") {
            payloadData = {
                id: responseData?.mcoPropeties?.id,
                producthub: `${responseData?.mcoPropeties?.name}_${responseData?.mcoPropeties?.plantName}`,
                uniquePartId: `${responseData?.mcoPropeties?.name}_${responseData?.mcoPropeties?.plantName}`,
                mcoProperties: updatedPropetiesValue,
            };
        } else {
            payloadData = {
                id: responseData?.mcoPropeties?.id,
                producthub: `${responseData?.mcoPropeties?.name}_${responseData?.mcoPropeties?.plantName}`,
                uniquePartId: `${responseData?.mcoPropeties?.name}_${responseData?.mcoPropeties?.plantName}`,
                mcoProperties: updatedPropetiesValue,
                mcoaffectedItem: responseData?.mcoaffectedItem,
                source: responseData?.source,
                target: responseData?.target
            };
        }
        const updateOrAddAttribute = async (payloadData) => {
            try {
                const updateFunction = (Object.keys(resData?.data).length && resData?.data[0]) ? mcoService.updateattributesbyid : mcoService.addattributes;
                const getUpdateResponse = await updateFunction(payloadData);
                if (saveMode === "saveMcoState" && Object.keys(getUpdateResponse?.data || {}).length) {
                    const updateResData = getUpdateResponse?.data || {};
                    await Store.dispatch(updateMcoDetails({
                        mcoPropeties: updateResData?.mcoProperties,
                        mcoaffectedItem: updateResData?.mcoaffectedItem,
                    }));
                    await prepareAffectedPartData();
                }
            } catch (err) {
                console.log(err);
                return { status: "failed", data: err?.message };
            }
        };
        await updateOrAddAttribute(payloadData);
    } catch (error) {
        console.log(error);
        return { status: "failed", data: error?.message };
    }
};
const getAffectedPartsData = async () => {
    const getStorevalue = Store.getState();
    const responseData = getStorevalue?.mcoDetails?.responseData || {};
    const affectedPartsData = getStorevalue?.mcoDetails?.affectedPartsData || [];
    if (affectedPartsData?.length) {
        return affectedPartsData || [];
    } else {
        return responseData?.mcoaffectedItem || [];
    }
}
const handleMaterialTypeSelect = async (selectedValue, thisRowId = 0, dropType) => {
    const getStorevalue = Store.getState();
    const templateMappingsInfo = getStorevalue.mcoDetails.templateMappingsInfo || [];
    const responseData = getStorevalue.mcoDetails.responseData || {};
    const materialData = getStorevalue.mcoDetails.materialData || [];

    // let mcoaffectedItem = [...responseData.mcoaffectedItem];
    const affectedPartsData = await getAffectedPartsData();
    let mcoaffectedItem = [...affectedPartsData];

    let thisRowIndex = mcoaffectedItem?.findIndex(foption => {
        return foption.id === thisRowId;
    });

    // console.log(`Selected value: ${selectedValue} and thisRowIndex: ${thisRowIndex}`);

    if (dropType === "template") {
        let findMaterialName = (materialData?.filter(foption => {
            return foption.id === selectedValue;
        }) || []);
        findMaterialName = (findMaterialName?.length) ? findMaterialName[0] : null;
        mcoaffectedItem[thisRowIndex]['materialType'] = (findMaterialName) ? { name: findMaterialName?.templateName } : null;
        mcoaffectedItem[thisRowIndex]['isTemplateValueChange'] = true;

        // Find makeORBuy value and assign it
        let partBusinessUnitValue = mcoaffectedItem[thisRowIndex]?.organization;
        let buplantmapping_RDOPlants = templateMappingsInfo?.find((acitem) => {
            return acitem.MappingType === 'buplantmapping' && acitem.BUID?.toLowerCase() === partBusinessUnitValue?.toLowerCase() && acitem?.details?.Entity.toLowerCase() === "bom or no bom";
        }) || {}
        let hasMbomTemplateList = templateMappingsInfo?.filter((acitem) => {
            return acitem.MappingType === 'orgallowedvalues' && acitem.BUID?.toLowerCase() === partBusinessUnitValue?.toLowerCase() && acitem?.details?.Has_MBOM?.toLowerCase() === "y";
        }).map((eitem) => {
            return eitem?.details?.ALLOWABLE_ORG_TEMPLATES;
        }) || [];

        let isExceptionalPlant = false;
        let hasMbomTemplate = false;
        if (Object.keys(hasMbomTemplateList).length) {
            if (hasMbomTemplateList?.includes(findMaterialName?.templateName)) {
                hasMbomTemplate = true;
            }
        }
        if (Object.keys(buplantmapping_RDOPlants).length) {
            const plantValue = buplantmapping_RDOPlants?.details?.PLANT;
            const getPlantList = Object.keys(plantValue).filter((ekeys) => {
                return plantValue[ekeys]?.toLowerCase() === "no bom"
            });
            if (getPlantList?.includes(responseData?.mcoPropeties?.plantName)) {
                isExceptionalPlant = true;
            }
        };
        if (hasMbomTemplate && !isExceptionalPlant) {
            mcoaffectedItem[thisRowIndex]['makeOrBuy'] = "make";
            mcoaffectedItem[thisRowIndex]['isMakeOrBuyValueChange'] = true;
        } else {
            mcoaffectedItem[thisRowIndex]['makeOrBuy'] = "buy";
            mcoaffectedItem[thisRowIndex]['isMakeOrBuyValueChange'] = true;
        }
    }

    if (dropType === "exportToERP") {
        mcoaffectedItem[thisRowIndex]['exportToERP'] = selectedValue;
        mcoaffectedItem[thisRowIndex]['isExportToERPValueChange'] = true;
    }

    if (dropType === "itemStatus") {
        mcoaffectedItem[thisRowIndex]['itemStatus'] = selectedValue;
        mcoaffectedItem[thisRowIndex]['isItemStatusValueChange'] = true;
    }

    if (dropType === "updateRowData") {
        mcoaffectedItem[thisRowIndex]['newRevision'] = selectedValue?.newRevision;
        mcoaffectedItem[thisRowIndex]['oldRevision'] = selectedValue?.oldRevision;
        mcoaffectedItem[thisRowIndex]['currentSequence'] = selectedValue?.currentSequence;

        await Store.dispatch(update_generalValues({
            affectedPartsData: mcoaffectedItem,
        }));
    } else {
        await Store.dispatch(update_generalValues({
            affectedPartsData: mcoaffectedItem,
            isMaterialFormEdited: true
        }));
    }


    return mcoaffectedItem;
}
const findLabelForTooltip = (findMaterialDetails) => {
    const getStorevalue = Store.getState();
    const materialData = getStorevalue.mcoDetails.materialData || [];
    if (!findMaterialDetails.id) {
        return;
    }
    const selectedOption = materialData?.find(
        (option) => option.id === findMaterialDetails.id
    );
    return (selectedOption?.templateName) ? selectedOption.templateName : '';
}
const findDropdownValue = (affval) => {
    const getStorevalue = Store.getState();
    const masterData = getStorevalue.mcoDetails.masterData || {};
    const materialData = getStorevalue.mcoDetails.materialData || [];
    let findMaterialDetails = {};
    if (masterData && Object.keys(masterData).length && masterData?.master === "N" && masterData?.mcoaffectedItem) {
        let affkey = masterData?.mcoaffectedItem?.findIndex(foption => {
            return foption.id === affval?.id;
        });

        findMaterialDetails = materialData?.filter((foption) => {
            return foption.templateName === masterData?.mcoaffectedItem[affkey]?.materialType?.name;
        }) || [];

        findMaterialDetails = (findMaterialDetails?.length) ? findMaterialDetails[0] : {};

    } else if (materialData?.length) {
        findMaterialDetails = materialData?.filter((foption) => {
            return ((affval?.materialType && foption.templateName === affval?.materialType?.name) || (foption.desc === affval?.attributeGroupData?.ERPItemTypeTemplate));
        })

        findMaterialDetails = (findMaterialDetails?.length) ? findMaterialDetails[0] : {};
    }

    return findMaterialDetails;
}
const prepareAffectedPartData = async () => {
    const getStorevalue = Store.getState();
    const responseData = getStorevalue.mcoDetails.responseData || {};
    const materialData = getStorevalue.mcoDetails.materialData || [];
    const mrDetails = getStorevalue.mcoDetails.mrDetails || [];
    const eachTemplateOptions = getStorevalue.mcoDetails.eachTemplateOptions || {};
    let dropdownOpts = {};
    if (responseData?.mcoaffectedItem?.length) {
        // For accessing external file data
        if (window?.ExternalClass) {
            let data = {
                affectedItemsInfo: responseData,
            };
            // Create an instance of the ExternalClass class
            const externalClass = new window.ExternalClass(data);
            // Call the getDropdownOptions method with a name
            dropdownOpts = await externalClass.getDropdownOptions();
        }
        responseData?.mcoaffectedItem?.forEach((affval, affkey) => {
            let findMaterialDetails = findDropdownValue(affval, affkey);
            let thisDropOptions = eachTemplateOptions?.[affval?.name] || [];

            if (Object.keys(dropdownOpts)?.length && Object.keys(dropdownOpts)?.includes(affval?.name)) {
                thisDropOptions = dropdownOpts[affval?.name];
            }
            const templExistOrNot = thisDropOptions?.find(mtemp => {
                return mtemp?.id === findMaterialDetails.id;
            }) || {};
            if (!Object.keys(templExistOrNot)?.length && Object.keys(findMaterialDetails)?.length) {
                let prevSelectedTemplateObj = materialData?.find((foption) => {
                    return findMaterialDetails?.Name === foption.templateName;
                });
                if (prevSelectedTemplateObj) {
                    thisDropOptions = [...thisDropOptions, prevSelectedTemplateObj]
                }
            }

            const activeMRData = (Object.keys(mrDetails)?.length && mrDetails?.activeMR?.hasOwnProperty(affval?.name) && mrDetails?.activeMR[affval?.name]?.length) ? mrDetails?.activeMR[affval?.name][0] : {};
            const newRevision = Object.keys(activeMRData)?.length ? `${affval?.revision}${activeMRData?.additionalSequence}` : "";
            const currentSequence = Object.keys(activeMRData)?.length ? `${activeMRData?.sequence}` : "";

            const previousSeqMRData = (Object.keys(mrDetails)?.length && mrDetails?.activeMR?.hasOwnProperty(affval?.name) && mrDetails?.previousSeqMR[affval?.name]?.length) ? mrDetails?.previousSeqMR[affval?.name][0] : {};
            const oldRevision = Object.keys(previousSeqMRData)?.length ? `${previousSeqMRData?.revision}${previousSeqMRData?.additionalSequence}` : "";

            // update row data with new revisions
            handleMaterialTypeSelect({
                newRevision: newRevision,
                oldRevision: oldRevision,
                currentSequence: currentSequence,
            }, affval?.id, 'updateRowData');

            // getting active mr template for existing data from enovia
            const activeMRtemplExistOrNot = thisDropOptions?.find(mtemp => {
                return mtemp?.id === findMaterialDetails?.id;
            }) || {};
            if (!Object.keys(activeMRtemplExistOrNot)?.length) {
                let activeTemplateObj = materialData?.find((foption) => {
                    return activeMRData?.template === foption.templateName;
                });
                if (activeTemplateObj && !affval?.isTemplateValueChange) {
                    thisDropOptions = [...thisDropOptions, activeTemplateObj]
                    findMaterialDetails = activeTemplateObj;
                    handleMaterialTypeSelect(findMaterialDetails?.templates, affval?.id, 'template');
                }
            }
            let defaultOptValue = {};
            if (Object.keys(previousSeqMRData || {})?.length) {
                let previousSeqMRTemplateObj = materialData?.find((foption, findex) => {
                    return foption?.templateName === previousSeqMRData?.template;
                }) || {};
                if (Object.keys(previousSeqMRTemplateObj)?.length) {
                    thisDropOptions.push(previousSeqMRTemplateObj); // push the option to dropdown 
                    if (!affval?.isTemplateValueChange) {
                        defaultOptValue = previousSeqMRTemplateObj; // for selecting the option
                    }
                }
            }
            if (!Object.keys(findMaterialDetails || {})?.length && !affval?.isTemplateValueChange) {
                const isRevisionTempExist = thisDropOptions?.find((fcheckItem) => {
                    return fcheckItem?.templateName === previousSeqMRData?.template;
                }) || {};
                if (!Object.keys(isRevisionTempExist)?.length && Object.keys(defaultOptValue)?.length) {
                    findMaterialDetails = defaultOptValue;
                    handleMaterialTypeSelect(findMaterialDetails?.templates, affval?.id, 'template');
                } else if (Object.keys(isRevisionTempExist)?.length) {
                    findMaterialDetails = isRevisionTempExist;
                    handleMaterialTypeSelect(findMaterialDetails?.templates, affval?.id, 'template');
                }
            }
            let getExportToERPValue = affval?.exportToERP;
            let getItemStatusValue = (affval?.itemStatus)?.toLowerCase();
            if (!getExportToERPValue && !affval?.isExportToERPValueChange) {
                handleMaterialTypeSelect(getExportToERPValue, affval?.id, 'exportToERP')
            }
            if (!getItemStatusValue && !affval?.isItemStatusValueChange) {
                getItemStatusValue = activeMRData?.ERPStatus?.toLowerCase();
                handleMaterialTypeSelect(getItemStatusValue, affval?.id, 'itemStatus')
            }
        });
    }
    return;
}
const exportMCOData = async (propertiesData, exportAffectedPartsData) => {
    const getStorevalue = Store.getState();
    const responseData = getStorevalue.mcoDetails.responseData || {};
    const currentMcoDetails = getStorevalue.mcoDetails.currentMcoDetails || {};
    // const exportAffectedPartsData = getStorevalue.mcoDetails.exportAffectedPartsData || [];

    
    const mcoService = new MCOService();
    const affectedPartsData = await getAffectedPartsData();
    let mcoaffectedItemVal = affectedPartsData;
    if (exportAffectedPartsData?.length) {
        mcoaffectedItemVal = exportAffectedPartsData;
    } else {
        mcoaffectedItemVal = sortAffData({ key: 'name', direction: 'asc' });
    }

    const new_mcoaffectedItemVal = mcoaffectedItemVal?.map((eitem) => {
        let ErpStatus = responseData?.itemERPStatus && responseData?.itemERPStatus.filter((efitem) => {
            return efitem?.Item_Name === eitem?.name;
        });
        ErpStatus = (ErpStatus?.length) ? ErpStatus[0]?.status : 'Not yet processed!';
        eitem["ErpStatus"] = ErpStatus;
        return eitem;
    }) || [];

    const resData = await mcoService.exportMCOData({
        mcoProperties: propertiesData,
        mcoaffectedItem: new_mcoaffectedItemVal,
        ERPstatus: currentMcoDetails?.ERPstatus,
        ERPErrorDescription: currentMcoDetails?.ERPErrorDescription
    });
    console.log(resData);
    return resData;
}
const sortAffData = (sortConfig) => {
    const getStorevalue = Store.getState();
    const responseData = getStorevalue.mcoDetails.responseData || {};
    const affectedPartsData = getStorevalue.mcoDetails.affectedPartsData || [];

    let data = responseData?.mcoaffectedItem || [];
    if (affectedPartsData?.length) {
        data = affectedPartsData;
    }

    if (!sortConfig.key) return data;

    console.log(sortConfig, "=====sortConfig");

    let sortedData = [];
    if (sortConfig.key === "materialTypeTemplate") {
        sortedData = [...data].map(item => {
            item.materialType = (item?.materialType) ? item?.materialType : { name: "Unassigned" };
            return item;
        }).sort((a, b) => {
            if (a["materialType"]?.["name"] < b["materialType"]?.["name"]) return sortConfig.direction === 'asc' ? -1 : 1;
            if (a["materialType"]?.["name"] > b["materialType"]?.["name"]) return sortConfig.direction === 'asc' ? 1 : -1;
            return 0;
        }) || [];
    } else {
        sortedData = [...data].sort((a, b) => {
            if (a[sortConfig.key] < b[sortConfig.key]) return sortConfig.direction === 'asc' ? -1 : 1;
            if (a[sortConfig.key] > b[sortConfig.key]) return sortConfig.direction === 'asc' ? 1 : -1;
            return 0;
        }) || [];
    }

    console.log(sortedData, "========sortedData");
    return sortedData;
};
const trimmingDisplayContent = (ERPErrorDescription = null) => {
    let newERPErrorDescriptionVal = ERPErrorDescription;
    if (Array.isArray(ERPErrorDescription)) {
        newERPErrorDescriptionVal = newERPErrorDescriptionVal?.flat()?.join();
    }
    newERPErrorDescriptionVal = newERPErrorDescriptionVal?.replaceAll('/', '')
        .replaceAll('[', '')
        .replaceAll(']', '')
        .replaceAll('"', '')
        .replaceAll('"', '');
    return newERPErrorDescriptionVal;
}
